*
	padding: 0
	margin: 0
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0)
	&:focus
		outline: none

html, body
	width: 100%
	
	cursor: default
	&.header-active
		//overflow: hidden

body,html
	font: normal #{$default-font-size}/1.4 $font-family-base
	font-weight: 400
	color: $default-text-color
	background: #FFF
	+mobile
		-webkit-text-size-adjust: 100%

img, fieldset
	border: 0

fieldset legend
	display: none

ul, li
	list-style-type: none

img
	vertical-align: top

a
	color: $default-text-color
	text-decoration: none
	&:hover
		text-decoration: none

table
	border: 0
	border-collapse: collapse
	td
		padding: 0
		vertical-align: top

input, select, textarea
	+box

input[type=number]::-webkit-inner-spin-button
	-webkit-appearance: none
	margin: 0

input[type=number]::-webkit-outer-spin-button
	-webkit-appearance: none
	margin: 0

input[type=number]
	-moz-appearance: textfield

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration
	display: none

input[type=search]
	-webkit-appearance: textfield

::selection
	background: #5f5f5f
	color: #FFFFFF
	text-shadow: none

::-moz-selection
	background: #5f5f5f
	color: #FFFFFF
	text-shadow: none

#__bs_notify__
	padding: 2px !important
	font-size: 10px !important
	opacity: 0.75
	user-select: none
	pointer-events: none

button
	user-select: none
	border-radius: 0
	font-family: inherit
	cursor: pointer
input, textarea
	-webkit-appearance: none
	-moz-appearance: none
	appearance: none
	-webkit-border-radius: 0
	border-radius: 0
